aside {
	position: fixed;
	background-color: #fff;
	border: 1px solid #ccc;
	
	&#left {
		top: 10px;
		left: 10px;
		width: 450px;
		bottom: 10px;
	}
	&#rightTop {
		top: 10px;
		left: 470px;
		right: 10px;
		bottom: 50%;
		background-color: transparent;
		border: none;
		padding-bottom: 10px;
		
		> div {
			width: 100%;
			height: 100%;
			background-color: #fff;
			border: 1px solid #ccc;
		}
	}
	&#rightBottom {
		top: 50%;
		left: 470px;
		right: 10px;
		bottom: 10px;
	}
}